.catalog {
  padding-top: calc(65px - (1920px - 100vw)/(1920 - 770)*(65 - 55));
  padding-bottom: calc(114px - (1920px - 100vw)/(1920 - 770)*(114 - 40));
  background-color: $color-dark;
}

.catalog__title {
  @include font(36px, 51px, 400, $color-default-white);
  margin: 0 0 calc(50px - (1920px - 100vw)/(1920 - 770)*(50 - 40));
}

.catalog__title-mobile {
  display: none;
}

.catalog__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;

  @include vp-1439 {
    flex-direction: column;
    align-items: center;
  }
}

.catalog__item {
  position: relative;
  margin-bottom: 30px;

  &::after {
    content: "";
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: $color-lightblue;
    right: 0;
    bottom: 0;
    background-image: url(../img/svg/Vector.svg);
    background-repeat: no-repeat;
    background-position: center;

    @include vp-769 {
      width: 40px;
      height: 40px;
    }

  }

}

.catalog__item p {
  @include font(20px, 30px, 400, $color-default-white);
  position: absolute;
  top: calc(50px - (1920px - 100vw)/(1920 - 770)*(50 - 35));
  left: 40px;
  margin: 0;
}

@media (max-width: $vp-770) {


  .catalog__breake {
    display: none;
  }

  .catalog__title {

    @include vp-769 {
      display: none;
    }
  }

  .catalog__title-mobile {
    @include vp-769 {
      display: block;
      @include font(28px, 39px, 400, $color-default-white);
      margin: 0 0 30px;
    }
  }

  .catalog__item p {
    width: 150px;
  }
}

@media (max-width: $vp-320) {
  .catalog {
    padding-top: 45px;
    padding-bottom: 55px;
  }

  .catalog__item p {
    @include font(18px, 30px, 400, $color-default-white);
    left: 30px;
    top: 25px;
  }

}
